import Component from "../React/component"
import { diff, diffNode } from "./diff"
const ReactDom = {
  render
}

function render(vnode, container, dom) {
  return diff(dom, vnode, container)
}

export function createComponent(component, props) {
  let inst
  if (component.prototype && component.prototype.render) {
    // ç±»ç»„ä»¶ï¼Œåˆ›å»ºå®ä¾‹è¿”å›
    inst = new component(props)
  } else {
    // å‡½æ•°å¼ç»„ä»¶ï¼Œå°†ä¹‹æ‰©å±•æˆç±»ç»„ä»¶ï¼Œæ–¹ä¾¿ç»Ÿä¸€ç®¡ç†
    inst = new Component(props)
    inst.constructor = component
    inst.render = function () {
      return this.constructor(props)
    }
  }
  return inst
}

export function renderComponent(component) {
  let base
  const renderer = component.render() // jsx å¯¹è±¡

  // ç”Ÿå‘½å‘¨æœŸæ–¹æ³•
  if (component.base && component.componentWillUpdate) {
    component.componentWillUpdate()
  }

  if (!component.base && component.componentDidMount) {
    component.componentDidMount()
  }

  // base = _render(renderer)
  base = diffNode(component.base, renderer)
  component.base = base

  console.log(component);
  if (component.base) {
    if (component.componentDidUpdate) component.componentDidUpdate()
  } else if (component.componentDidMount) {
    component.componentDidMount()
  }

  // if (component.base && component.base.parentNode) {
  //   component.base.parentNode.replaceChild(base, component.base)
  // }
  component.base = base
}

export function setComponentProps(component, props) {
  // ç”Ÿå‘½å‘¨æœŸæ–¹æ³•
  if (!component.base) {
    if (component.componentWillMount) component.componentWillMount()
  } else if (component.componentWillReceiveProps) {
    component.componentWillReceiveProps(props)
  }
  // è®¾ç½®ç»„ä»¶å±æ€§
  component.props = props
  // æ¸²æŸ“ç»„ä»¶
  renderComponent(component)
}

function _render(vnode) {
  console.log(vnode);

  if (vnode === undefined || vnode === null || typeof vnode === 'boolean') return
  if (typeof vnode === 'number') vnode = String(vnode)
  if (typeof vnode === 'string') {
    const textNode = document.createTextNode(vnode)
    return textNode
  }
  // å¦‚æœtagä¸ºå‡½æ•°ï¼Œåˆ™æ¸²æŸ“ç»„ä»¶
  if (typeof vnode.tag === 'function') {
    // åˆ›å»ºç»„ä»¶
    const component = createComponent(vnode.tag, vnode.attrs)
    // è®¾ç½®ç»„ä»¶çš„å±æ€§
    setComponentProps(component, vnode.attrs)
    // ç»„ä»¶æ¸²æŸ“çš„èŠ‚ç‚¹å¯¹è±¡è¿”å›
    return component.base
  }

  const { tag, attrs } = vnode
  const dom = document.createElement(tag)
  if (attrs) {
    Object.keys(attrs).forEach(key => {
      const value = attrs[key]
      setAttribute(dom, key, value)
    })
  }
  // æ¸²æŸ“å­èŠ‚ç‚¹
  vnode.childrens && vnode.childrens.forEach(child => render(child, dom))
  return dom
}

export function setAttribute(dom, key, value) {
  // å¦‚æœå±æ€§ä¸ºç±»å
  if (key === 'className') key = 'class'
  // å¦‚æœå±æ€§ä¸ºäº‹ä»¶
  if (/on\w+/.test(key)) {
    key = key.toLowerCase()
    dom[key] = value
  } else if (key === 'style') {
    // å±æ€§ä¸ºcssæ ·å¼
    if (!value || typeof value === 'string') {
      dom.style.cssText = value || ''
    } else if (value && typeof value === 'object') {
      for (let k in value) {
        if (typeof value[k] === 'number') {
          dom.style[k] = value[k] + 'px'
        } else {
          dom.style[k] = value[k]
        }
      }
    }
  } else {
    // å…¶ä»–å±æ€§
    if (key in dom) {
      dom[key] = value || ''
    }
    if (value) {
      dom.setAttribute(key, value)
    } else {
      dom.removeAttribute(key)
    }
  }
}


export default ReactDom